import { IDisplayPreference } from './definitions';

const /** @type {?} */ arabic = new Intl.NumberFormat('ar-AE');
const /** @type {?} */ persian = new Intl.NumberFormat('fa-IR');

export const /** @type {?} */ PersianPreference: IDisplayPreference = {
  hour: (x) => persian.format(x),
  minute: (x) => {
    let /** @type {?} */ exp = persian.format(x);
    if (exp.length === 1) {
      exp = persian.format(0) + exp;
    }
    return exp;
  },
  separator: ':',
  period: (x) => x === 'AM' ? '' : '',
  clockHour: (x) => persian.format(x),
  clockMinute: (x) => persian.format(x),
  labels: {
    ok: '',
    cancel: ''
  }
};

export const /** @type {?} */ ArabicPreference: IDisplayPreference = {
  hour: (x) => arabic.format(x),
  minute: (x) => {
    let /** @type {?} */ exp = arabic.format(x);
    if (exp.length === 1) {
      exp = arabic.format(0) + exp;
    }
    return exp;
  },
  separator: ':',
  period: (x) => x === 'AM' ? '' : '',
  clockHour: (x) => arabic.format(x),
  clockMinute: (x) => arabic.format(x),
  labels: {
    ok: '',
    cancel: ''
  }
};

export const /** @type {?} */ ChinesePreference: IDisplayPreference = {
  hour:  (x) => x,
  minute: (x) => {
    let /** @type {?} */ exp = x;
    if (exp.length === 1) {
      exp = '' + exp;
    }
    return exp;
  },
  separator: ':',
  period: (x) => x === 'AM' ? '' : '',
  clockHour: (x) => x,
  clockMinute: (x) => x,
  labels: {
    ok: '',
    cancel: ''
  }
};

export const /** @type {?} */ Preference = (locale: string): IDisplayPreference => {
  switch (locale) {
    case 'fa':
        return PersianPreference;
    case 'ar':
        return ArabicPreference;
    case 'zh':
        return ChinesePreference;
    default:
      return null;
  }
};
